
import { defineComponent, reactive } from 'vue';
import Network from '@/components/Network.vue'; // @ is an alias to /src
import { useRoute } from 'vue-router'; 

export default defineComponent({
  name: 'Obj',
  components: {
    Network,
  },
  setup(){
    //data
    const data = reactive({
      panel_fmid:"",
      url_pathTop:""
    })

    const route = useRoute();
    
    data.panel_fmid = (route.params.fmid) as string;
    data.url_pathTop = "obj";

    return{
      data
    }
  }
});
